*{
  font-family: cursive;
  font-size: 15px;
    
}
body{
  padding: 50px;
}


input{

  color:green;
  font-size: 1rem;
  width: 100%;
}

.card-grid {
display: flex;
flex-direction: row;
flex-wrap: wrap;
}

.card{
align-content:center;
border: 2px solid green;
border-radius: 20px;
margin-bottom: 10px;
margin-right: 10px;
overflow: hidden;
}

.card p {
  padding: 3px;
  text-align: justify;
  font-size: 11px;
}

.card img{
  max-height: 200px;
}